import React from "react"
import { Link } from "gatsby"

export default function Layout({ children }) {
  return (
    <div className="w-full px-2 lg:px-40 xl:px-52 2xl:px-96 mx-auto">
      <div className="flex justify-between items-center mt-2 mb-5">
        <div>
          <Link to="/" className="cursor-pointer text-gray-800 text-2xl font-semibold font-logo subpixel-antialiased">
            <div>Finança</div>
            <div>Elementar</div>
          </Link>
        </div>
        <div></div>
        <div className="cursor-pointer text-gray-700">
          <Link to="mailto:financaelementar@gmail.com">
            <div className="flex items-center">
              <div>
                <svg className="w-6 h-6 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                  <path 
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2" d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z">
                  </path>
                </svg>
              </div>
              <div className="ml-1">
                financaelementar@gmail.com
              </div>
            </div>
          </Link>
        </div>
      </div>
      {children}
    </div>
  )
}